import React from 'react';
import { Box } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import { useTranslation } from 'react-i18next';
import Temoignages from '../temoignages/temoignage';
import Choix from '../mesChoix/choix';
import Sponsor from './sponsor';
import Langue from '../langues/langue';
import './medecin.css';

const Medecin = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();

  const handleContactClick = () => {
    navigate('/contact');
  };
  const handleTemoignageClick = () => {
    navigate('/toutTemoignages');
  };

  return (
    <Box className="medecin-container">
      <div className="flou">
        <img src="/images/bureau.jpeg" alt="avion" />
        <div className="backdrop-overlay">
          <h2 className="titre">{t('accueil.title')}</h2>
          <p className="descrire">{t('accueil.description')}</p>
          <button className="green-button" onClick={handleContactClick}>
            NOUS CONTACTER <ChevronRightIcon style={{ fontSize: '20px', marginLeft: '5px' }} />
          </button>
        </div>
      </div>
      <div className="choisir">
        <Choix />
      </div>
      <div className="presen-story">
      <div class="title-container">
  <h1 class="styled-title">QUI SOMMES-NOUS ?</h1>
  <p class="subtitle">Cabinet spcialis dans l'accompagnement des individus souhaitant immigrer et tudier  l'tranger !</p>
</div>

<div className="entete-container">
  <div className="entete">
    <div className="stori-text">
      <p>Cr en 2009, Bridge To Study est un cabinet spcialis dans le conseil en stratgie de formation et dorientation des tudiants dans leur projet dtudes  ltranger. Plaant lavenir des tudiants au cur de sa mission. Notre cabinet sest, depuis le dbut, engag  satisfaire au mieux leurs inquitudes, boostant de faite leur carrire de sorte  lui donner une dimension internationale. Cest pourquoi nous avons nou des partenariats souds avec les plus prestigieuses universits et grandes coles du monde pour garantir un avenir sr et des chances de succs relles  nos tudiants.</p>
    </div>
    <div className="stori-text">
      <p>Notre dmarche novatrice de lorientation scolaire permet, suite  un bilan de comptence, de reprer chez les tudiants, marocains ou trangers,
        davoir une vision globale et claire des futures perspectives universitaires et les diffrentes possibilits demploi associes  chaque choix acadmique,
        en se basant surtout sur ses traits de personnalit, ses motivations et ses souhaits.
        Notre cabinet met  la disposition de chaque lve et tudiant toute son expertise (plus de 15000 tudiants accompagns) ainsi quune panoplie doutils daide...
      </p>
      <button className="learn-more-button">En savoir plus</button>
    </div>
  </div>
</div>

        <div className="stori-video">
          <iframe
            width="1230"
            height="520"
            src="https://www.youtube.com/embed/LTAhX568Esk"
            title="Bridge To Study Video"
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          ></iframe>
        </div>
      </div>
      <div className="temoignage">
        <Temoignages />
      </div>
      <div className="tout-temoignage">
        <button className="green-button" onClick={handleTemoignageClick}>
          Lire tous les tmoignages <ChevronRightIcon style={{ fontSize: '20px', marginLeft: '5px' }} />
        </button>
      </div>
      <div className="langue">
        <Langue/>
      </div>
      <div className="temoignage">
        <img src="/images/download.jpeg" alt="avion" />
      </div>
      <div className="sponsor">
        <Sponsor/>
      </div>
    </Box>
  );
};

export default Medecin;
